import { Injectable } from '@angular/core';
import {Observable, Subject} from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class ShipmentService {
  createStatus: boolean;

  subjectId = new Subject();
  statusSubject = new Subject();
  statusesSubject = new Subject();

  constructor() { }

  getShipment(id: number) {
    /// this.http ...  (id)
    console.log('Hello from service get shipment', 'id', id)
    this.subjectId.next(id)
  }

  changeStatus(status: string) {
    this.statusSubject.next(status)
  }

  //progress status shipment
  addNewStatus(status) {
    console.log(status)
    this.statusesSubject.next(status)
  }
}
