import { Component, OnInit } from '@angular/core';

declare const $: any;
declare interface RouteInfo {
  path: string;
  title: string;
  icon: string;
  class: string;
}
export const ROUTES: RouteInfo[] = [
  { path: '/companies', title: 'Companies', icon: 'pe-7s-graph', class: '' },
  { path: '/contacts', title: 'Contacts', icon: 'pe-7s-users', class: '' },
  { path: '/shipments', title: 'Shipments', icon: 'pe-7s-note2', class: '' },
  {path: '/users', title: 'User Management', icon: 'pe-7s-id', class: ''},
  // {
  //   path: '/maps',
  //   title: 'Maps (dont need)',
  //   icon: 'pe-7s-map-marker',
  //   class: ''
  // },
  // {
  //   path: '/notifications',
  //   title: 'Notifications (dont need)',
  //   icon: 'pe-7s-bell',
  //   class: ''
  // }
];

@Component({
  selector: 'app-sidebar',
  templateUrl: './sidebar.component.html',
})
export class SidebarComponent implements OnInit {
  menuItems: any[];

  constructor() {}

  ngOnInit() {
    // SIDE bar menu
    this.menuItems = ROUTES.filter(menuItem => menuItem);
  }
  isMobileMenu() {
    if ($(window).width() > 991) {
      return false;
    }
    return true;
  }
}
